* {margin: 0; padding: 0;}

body {
  background: #28a745;
}

.table {
  width: 340px;
  height: 240px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -170px 0 0 -170px;
  background: #fff2;
}
.table-half {
  width: 50%;
  height: 100%;
  float: left;
}
.table-left {
  float: left;
}
.table-right {
  float: right;
}

.card {
  position: relative;
  width: 150px;
  height: 220px;
  margin: 10px;
  border-radius: 10px;
  background: #fff;
  -webkit-box-shadow: 3px 3px 7px rgba(0,0,0,0.3);
  box-shadow: 3px 3px 7px rgba(0,0,0,0.3);
}
.card-shirt {
  background-color: #a94442;
  width: 146px;
  height: 216px;
  margin: 2px;
  position: absolute;
  border-radius: 10px;
}
.card p {
  text-align: center;
  font: 100px/220px Georgia, Times New Roman, serif;
}





